import { REQUEST_STATE_CHANGE, REQUEST_SUCCESS, REQUEST_FAILURE } from 'store/mutations'
import ZRequest from 'util/ZRequest'

export default {
  [REQUEST_STATE_CHANGE] (state, { name, status }) {
    state[name].status = status
    if (status === ZRequest.LOADING) {
      state[name].data = []
      // state[name].count = 0
    }
  },
  [REQUEST_SUCCESS] (state, { name, type, response }) {
    if (type === ZRequest.LIST) {
      state[name].data = response.results
      state[name].count = response.count
    } else {
      state[name].data = response
    }
  },
  [REQUEST_FAILURE] (state, { name, type }) {
    if (type === ZRequest.LIST) {
      state[name].data = []
      state[name].count = 0
    } else {
      state[name].data = {}
    }
  }
}
