import Vue from 'vue'
import App from '@/App'
import router from 'router'
import store from 'bizStore'
import * as Package from 'plugin/Package'
import Element from 'element-ui'
import '@/assets/scss/element-variables.scss'
Vue.use(Element)
Vue.config.productionTip = false
Vue.prototype.IS_DEV = process.env.VUE_APP_ENV === 'development'
// 所有资源统一入口
Vue.use(Package)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
