import translation from './translation.js'
// { chunk, prop }
import * as util from './util.js'
let f = {
  translation
}
f = Object.assign(f, util)
export default {
  install (Vue) {
    Object.keys(f).forEach(name => {
      let filter = f[name]
      Vue.filter(name, filter)
      Vue.prototype['$' + name] = filter
    })
  }
}
