<template>
  <div class="q-index-search-header">
    <div class="q-index-search-header-left">
      <div class="q-index-search-header-item"
        :class="{active: isActive(item)}"
        v-for="item in items"
        :key="item.label"
        @click="select(item)"
      >{{item.label}}</div>
    </div>
    <div class="q-index-search-header-right"
      @click="advancedSearchHandler"
    >
      <span>高级查询</span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'QIndexSearchHeader',
  data () {
    return {
      items: [
        { label: '全部', value: '' },
        { label: '机构', value: 'org' },
        { label: '项目', value: 'project' },
        { label: '资讯', value: 'news' }
      ],
      active_item: ''
    }
  },
  methods: {
    isActive (item) {
      return this.active_item === item.value
    },
    select (item) {
      this.active_item = item.value
    },
    advancedSearchHandler () {
      this.$emit('click-right')
    }
  }
}
</script>
<style lang="scss">
.q-index-search-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 850px;
  user-select: none;
  margin-bottom: 10px;
  .q-index-search-header-left {
    display: flex;
    align-items: center;
  }
  .q-index-search-header-right {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  .q-index-search-header-item {
    display: flex;
    padding: 8px 10px;
    cursor: pointer;
    &.active {
      background: #fff;
      color: #10268C;
      font-size: 14px;
      position: relative;
      &::after {
        content: '';
        display: block;
        width: 0;
        height: 0;
        position: absolute;
        border-top: 6px solid #fff;
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        bottom: -6px;
        left: 50%;
        margin-left: -6px;
      }
    }
  }
}
</style>
