<template>
  <div class="q-index-search-footer">
    <div class="mr10" style="font-weight: 700;">热搜:</div>
    <div class="q-index-search-footer-items">
      <div class="q-index-search-footer-item"
        v-for="item in hotKeywords"
        :key="item.label"
      >{{item.label}}</div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'QIndexSearchFooter',
  props: {
    hotKeywords: {
      type: Array,
      default () {
        let items = []
        let i = 0
        while (i++ < 20) {
          items.push({ label: '机构' + i, name: '', query: '' })
        }
        return items
      }
    }
  }
}
</script>
<style lang="scss">
.q-index-search-footer {
  display: flex;
  // justify-content: flex-start;
  width: 850px;
  margin-top: 20px;
  overflow: hidden;
  .q-index-search-footer-items {
    display: flex;
    flex: 1;
    overflow: hidden;
    flex-wrap: wrap;
    height: 22px;
  }
  .q-index-search-footer-item {
    height: 20px;
    border: 1px solid #fff;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 0 10px;
    font-size: 12px;
    &:hover {
      background: #fff;
      color: #000;
      cursor: pointer;
      user-select: none;
      transition: all .3s
    }
  }
}
</style>
