const comp = require.context('./', true, /\.(vue|js)/)
const components = comp.keys()
  .filter(name => !name.includes('index.js'))
  .map(comp).map(eachComp => eachComp.default)

export default {
  install (Vue) {
    components.forEach(component => {
      Vue.component(component.name, component)
    })
  }
}
