import * as commonDicts from 'constant/dicts'
import * as bizDicts from 'bizConstant/dicts'
// import { warn } from 'util'
// bizDicts优先
const dicts = Object.assign({}, commonDicts, bizDicts)

function isEmpty (obj) {
  if (obj !== 0 && !obj) {
    return true
  }
  if (Array.isArray(obj) && obj.length === 0) {
    return true
  }
  if (typeof obj === 'object' && Object.keys(obj) === 0) {
    return true
  }
  return false
}
function check (value, rs) {
  if (!rs && !isEmpty(value)) {
    // warn('尝试翻译[' + JSON.stringify(value) + ']失败')
  }
}
export default function translation (value, dictName, dot = ', ') {
  let dict = dicts[dictName]
  if (isEmpty(value)) {
    return ''
  }
  if (Array.isArray(value)) {
    let rs = value.map(item => {
      if (!dict[item]) {
        // warn('在进行数组翻译时, 翻译项[' + JSON.stringify(item) + ']时失败')
      }
      return dict[item]
    }).filter(item => item).join(dot)
    check(value, rs)
    return rs
  }
  let rs = dict[value]
  check(value, rs)
  return rs
}
