<template>
  <div id="root">
    <router-view />
    <command-line></command-line>
  </div>
</template>

<style lang="scss">
// 全局公共样式
// 在App.vue中导入
// 避免在编译代码时出现重复代码
@import "base/assets/scss/common.scss";
</style>
<script>
import Vue from 'vue'
export default {
  computed: {
    is_ie () {
      return !!window.ActiveXObject || 'ActiveXObject' in window
    }
  },
  mounted () {
    Vue.prototype.IS_IE = this.is_ie
    if (document.body && this.is_ie) {
      document.body.className += ' is-ie'
    }
  }
}
</script>
