import _ from 'lodash'
import Vue from 'vue'
import { isEmpty } from 'util/isEmpty'
import { hasProp } from 'util'
export function chunk (value, size = 2) {
  return _.chunk(value, size)
}
export function prop (value, prop_name) {
  if (!value) {
    return ''
  }
  if (prop_name.indexOf('.') === -1) {
    return value[prop_name]
  } else {
    let prop_path = prop_name.split('.')
    let i = 0
    let l = prop_path.length
    for (;i < l; i += 1) {
      let path = prop_path[i]
      if (value) {
        value = value[path]
      } else {
        return value
      }
    }
    return value
  }
}
export function dateformat (value) {
  if (_.isString(value)) {
    value = value.replace('T', ' ')
    value = value.split('.')[0]
  }
  return value
}
export function join (value, dot = '，') {
  if (Array.isArray(value)) {
    return value.join(dot)
  }
  return value
}
export function value (input, itemConfig = {}) {
  if (!input) return ''
  let { prop, filter, filter_params } = itemConfig
  let placeholder
  if (hasProp(itemConfig, 'placeholder')) {
    placeholder = itemConfig.placeholder
  } else {
    placeholder = '- -'
  }
  if (!filter) {
    return input[prop] || placeholder
  } else {
    let _filter = Vue.prototype['$' + filter]
    let origin_data = input[prop]
    let args = [origin_data]
    if (filter_params) {
      args = args.concat(filter_params)
    }
    let value = _filter.apply(this, args)
    return value || '- -'
  }
}

export function empty (value) {
  return isEmpty(value)
}
export function show (value) {
  return !isEmpty(value)
}
export function hide (value) {
  return isEmpty(value)
}
