import _ from 'lodash'
// warn
export function warn (msg) {
  console.warn('[APP WARN]' + msg)
}

// 检测属性是否存在
export function hasProp (obj, propName) {
  if (!obj) return false
  return Object.prototype.hasOwnProperty.call(obj, propName)
}

// 在obj上创建一个属性, 若不存在该属性才创建
export function defProp (obj, key, value) {
  if (!hasProp(obj, key)) {
    Object.defineProperty(obj, key, {
      enumerable: false,
      writable: true,
      value
    })
  }
}
/**
 * Deep diff between two object, using lodash
 * @param  {Object} object Object compared
 * @param  {Object} base   Object to compare with
 * @return {Object}        Return a new object who represent the diff
 */
export function diff (object, base) {
  function changes (object, base) {
    return _.transform(object, function (result, value, key) {
      if (!_.isEqual(value, base[key])) {
        result[key] = (_.isObject(value) && _.isObject(base[key]))
          ? changes(value, base[key])
          : value
      }
    })
  }
  return changes(object, base)
}

function formatDateNumber (num) {
  return num < 10
    ? '0' + num
    : '' + num
}

export function dateFormat (date) {
  if (date instanceof Date) {
    let year = date.getFullYear()
    let _month = date.getMonth() + 1
    let _day = date.getDate()
    let month = formatDateNumber(_month)
    let day = formatDateNumber(_day)
    return [year, month, day].join('-')
  }
}
