<template>
  <transition name="el-fade-in">
    <div class="ScrollToTop" v-show="visiable" @click="toTop">
      <i class="el-icon-arrow-up"></i>
      <span>置顶</span>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'ScrollToTop',
  data () {
    return {
      visiable: false
    }
  },
  created () {
    let that = this
    that.scrollHandler = function (e) {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      let clientHeight = document.documentElement.clientHeight || document.body.clientHeight
      if (scrollTop > clientHeight / 2 && !that.visiable) {
        that.visiable = true
      } else if (scrollTop < clientHeight / 2 && that.visiable) {
        that.visiable = false
      }
    }
    window.addEventListener('scroll', that.scrollHandler)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.scrollHandler)
  },
  methods: {
    toTop () {
      window.scrollTo(0, 0)
    }
  }
}
</script>
<style lang="scss">
.ScrollToTop {
  position: fixed;
  z-index: 999;
  bottom: 400px;
  right: 150px;
  width: 50px;
  height: 60px;
  // padding: 10px;
  background: #fff;
  box-shadow: 0 0 3px rgba(0, 0, 0, .5);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: $--color-primary;
  cursor: pointer;
  user-select: none;
  transition: all .3s;
  i {
    font-weight: 700;
    font-size: 20px;
  }
  &:hover {
    box-shadow: 0 0 5px rgba(0, 0, 0, .6);
  }
}
</style>
