function install (Vue) {
  let press_keys = []
  let timer = null
  let key_map = {
    control: 'ctrl',
    escape: 'esc'
  }
  document.addEventListener('keydown', e => {
    let key = e.key.toLowerCase()
    press_keys.push(key_map[key] ? key_map[key] : key)
    console.log(press_keys)
    if (timer) {
      clearTimeout(timer)
    }
    // 组合键需要在1s内按下
    timer = setTimeout(() => {
      press_keys = []
    }, 300)
    hot_key_handlers(e)
  })
  let hot_keys = {
    'ctrl+p': [function (e) { console.log('got it') }],
    'ctrl+s': [function (e) { console.log('got it') }],
    'ctrl+e': [function (e) { console.log('got it') }],
    'ctrl+k': [function (e) { console.log('got it') }],
    'ctrl+l': [function (e) { console.log('got it') }],
    'ctrl+k+l': [function (e) { console.log('got it k l') }],
    'e+e': [function (e) { console.log('got it') }]
  }
  function hot_key_handlers (e) {
    // 遍历注册的快捷键
    Object.keys(hot_keys).forEach(function (hot_key) {
      let fns = hot_keys[hot_key]
      // 命中快捷键
      if (hit_hot_key(hot_key, e) && fns.length > 0) {
        // 取消默认操作
        e.preventDefault()
        for (let i = 0; i < fns.length; i += 1) {
          fns[i](e)
        }
      }
    })
  }
  // 子集判断
  function isSubset (_a, b) {
    let a = _a.slice()
    for (let i = 0, len = b.length; i < len; i++) {
      if (a.indexOf(b[i]) === -1) {
        return false
      } else {
        a.splice(a.indexOf(b[i]), 1)
      }
    }
    return true
  }
  // 是否命中快捷键
  function hit_hot_key (hot_key, e) {
    let control_keys = ['ctrl', 'shift', 'alt', 'meta']
    let controls = {}
    control_keys.forEach(key => Object.defineProperty(controls, key, { get () { return e[key + 'Key'] } }))
    let keys = hot_key.split('+').filter(k => k).map(k => k.trim())
    let rs = []
    control_keys.forEach(key => {
      let i = keys.indexOf(key)
      if (i > -1) {
        rs.push(controls[key])
        keys.splice(i, 1)
      }
    })
    rs.push(isSubset(press_keys, keys))
    return !rs.some(x => !x)
  }
  Vue.mixin({
    methods: {
      addHotKey (keys, fn) {
        if (typeof fn === 'function') {
          (hot_keys[keys] || (hot_keys[keys] = [])).push(fn)
        }
      },
      removeHotKey (keys, fn) {
        if (!fn) {
          hot_keys[keys] = []
        } else {
          let i = hot_keys[keys].indexOf(fn)
          if (i > -1) {
            hot_keys.splice(i, 1)
          }
        }
      }
    }
  })
}

export {
  install
}
