<template>
  <div class="q-header" :class="is_home ? 'is-home' : 'is-normal'">
    <div class="q-header-container">
      <div class="q-header-left" v-show="is_home">
        <img src="~img/logo.png" style="width: 138px; height: 28px;">
        <span class="ml20">公益数据库</span>
      </div>
      <div class="q-header-left" v-show="!is_home" @click="toIndex">首页</div>
      <div class="q-header-right">
        <div class="q-header-item"
          v-for="item in items"
          :key="item.label"
          @click="toPage(item)"
        >{{item.label}}</div>
        <div class="q-header-login-container">
          <div v-show="!is_login" class="q-header-login-btn">登录/注册</div>
          <div v-show="is_login" class="q-header-after-login"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'QHeader',
  data () {
    return {
      items: [
        { label: '公益资讯', name: 'News' },
        { label: '志愿者招募', name: 'VolunteerRecruit' },
        { label: '关于我们', name: 'AboutUs' }
      ],
      is_login: false
    }
  },
  computed: {
    // 是否为首页
    is_home: vm => vm.$route.name === 'Home'
  },
  methods: {
    toPage (item) {
      this.$router.push({ name: item.name })
    },
    toIndex () {
      this.$router.push({ name: 'Home' })
    },
    toLogin () {
      this.$router.push({ name: 'Login' })
    }
  }
}
</script>
<style lang="scss">
.q-header {
  height: 50px;
  display: flex;
  align-items: center;
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99999;
  .q-header-container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  .q-header-left {
    display: flex;
    align-items: center;
  }
  .q-header-right {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  .q-header-item {
    padding: 0 25px;
    height: 50px;
    display: flex;
    align-items: center;
    &:hover {
      background: #eee;
    }
  }
  .q-header-login-container {
    margin-left: 25px;
  }
  .q-header-login-btn {
    text-align: center;
    width:78px;
    height:30px;
    line-height: 30px;
    font-size: 14px;
    background:rgba(137,231,117,1);
    border:1px solid rgba(40,181,100,1);
    opacity:0.3;
    border-radius:2px;
    &:hover {
      opacity: .2;
    }
    &:active {
      opacity: .4;
    }
  }
  // 非首页头部
  &.is-normal {
    height: 40px;
    background: #212A37;
    color: #fff;
    .q-header-left {
      cursor: pointer;
      &:hover {
        color: $--color-primary;
      }
    }
    .q-header-item {
      background: none;
      &:hover {
        color: $--color-primary;
      }
    }
    .q-header-login-btn {
      opacity: 1;
      @include hover();
    }
  }
}
</style>
