import apis from 'api/index'
import _ from 'lodash'
import ZRequest from 'util/ZRequest'
let getters = {}
_.each(apis, api => {
  getters[api.name + '_is_loading'] = (state) => {
    return state[api.name].status === ZRequest.LOADING
  }
  getters[api.name + '_is_success'] = (state) => {
    return state[api.name].status === ZRequest.SUCCESS
  }
  getters[api.name + '_is_repeat'] = (state) => {
    return state[api.name].status === ZRequest.REPEAT
  }
})
export default getters
