import { isEmpty } from 'util/isEmpty'
export function visible (el, binding) {
  if (isEmpty(binding.value)) {
    el.style.display = 'none'
  } else {
    el.style.display = ''
  }
}
export function hidden (el, binding) {
  if (isEmpty(binding.value)) {
    el.style.display = ''
  } else {
    el.style.display = 'none'
  }
}
