import { SAVE_TO_DATA_POOL, UPDATE_TO_DATA_POOL } from 'store/mutations'
import ZRequest from 'util/ZRequest'
import Vue from 'vue'
export default {
  [SAVE_TO_DATA_POOL] (state, {
    // list or detail or other
    type,
    // 数据类型
    data_type,
    // 请求结果
    response
    // ,
    // // 用于进行唯一标识的字段
    // uuid
  }) {
    if (!state[data_type]) {
      state[data_type] = {}
    }
    if (type === ZRequest.LIST) {
      let rs = response.results
      rs.forEach(item => {
        let _uuid = item._uuid
        state[data_type][_uuid] = item
      })
    } else {
      let _uuid = response._uuid
      state[data_type][_uuid] = response
    }
  },
  [UPDATE_TO_DATA_POOL] (state, {
    path,
    value
  }) {
    let s = state
    let last_path = path.splice(-1, 1)
    let i = 0
    let len = path.length
    for (; i < len; i += 1) {
      let p = path[i]
      s = s[p]
      if (!s) return
    }
    Vue.set(s, last_path, value)
    // s[last_path] = value
  }
}
