import Vue from 'vue'
import Router from 'vue-router'
import Home from 'view/Home'
import Index from 'view/Index'
// Exception 异常页面
import ExceptionHome from 'view/exception/Home'
import ExceptionRouter from 'router/exception'
// 公益资讯
import NewsHome from 'view/news/Home'
import NewsRouter from 'router/news'
// 公益资讯
import VolunteerRecruitHome from 'view/volunteer_recruit/Home'
import VolunteerRecruitRouter from 'router/volunteer_recruit'
// 关于我们
import AboutUsHome from 'view/about_us/Home'
import AboutUsRouter from 'router/about_us'

import NotFound from 'view/exception/NotFound'
Vue.use(Router)

let router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior (to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash
      }
    }
  },
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          // 首页
          name: 'Home',
          path: '',
          component: Index
        },
        {
          path: 'news',
          component: NewsHome,
          children: NewsRouter
        },
        {
          path: 'volunteer_recruit',
          component: VolunteerRecruitHome,
          children: VolunteerRecruitRouter
        },
        {
          path: 'about_us',
          component: AboutUsHome,
          children: AboutUsRouter
        },
        {
          path: 'exception',
          component: ExceptionHome,
          children: ExceptionRouter
        },
        {
          path: '*',
          component: NotFound
        }
      ]
    }
  ]
})
let routeStack = []
function isReplace (_to, _from) {
  return _to.name === _from.name
}
router.beforeEach((_to, _from, next) => {
  if (isReplace(_to, _from)) {
    routeStack.pop()
  }
  routeStack.push(_to)
  console.log(routeStack.map(r => r.name))
  next()
})
export default router
