import Vue from 'vue'
const SET_PAGE = 'SET_PAGE'
let state = {}
let actions = {
  setPage ({ commit }, { name, page }) {
    commit(SET_PAGE, { name, page })
  },
  initPage ({ commit }, { name }) {
    commit(SET_PAGE, { name, page: 1 })
  }
}
let mutations = {
  [SET_PAGE] (state, { name, page }) {
    let item = state[name] || {}
    Vue.set(state, name, Object.assign(item, { page }))
  }
}

export default {
  state,
  actions,
  // getters,
  mutations
}