function install (Vue) {
  function notice_children (event, args) {
    let children = this.$children.map(ch => ch)
    // 广度遍历空间优化
    while (children.length > 0) {
      let child = children.shift()
      if (child.$children.length > 0) {
        children = children.concat(child.$children)
      }
      child.$emit.apply(child, [event].concat(args))
      if (child.__NOTICE_STOP__) {
        child.__NOTICE_STOP__ = false
        break
      }
    }
  }
  function notice_parent (event, args) {
    var parent = this.$parent || this.$root
    // console.log(parent.$el)
    while (parent) {
      parent.$emit.apply(parent, [event].concat(args))
      if (parent.__NOTICE_STOP__) {
        parent.__NOTICE_STOP__ = false
        break
      }
      parent = parent.$parent
    }
  }
  function notice_stop () {
    this.__NOTICE_STOP__ = true
  }
  // 挂载$开头的函数需要直接挂载到原型链上
  Vue.prototype.$notice_parent = notice_parent
  Vue.prototype.$notice_children = notice_children
  Vue.prototype.$notice_stop = notice_stop
}
export {
  install
}
