let _seed = 1
let keys = []
let seeds = []
const IS_DEV = process.env.VUE_APP_ENV === 'development'
function hasCache (item) {
  let index = keys.indexOf(item)
  return index !== -1
}
function cache (item, seed) {
  // let index = keys.indexOf(item)
  keys.push(item)
  seeds.push(seed)
}
function gen_key (item) {
  if (hasCache(item)) {
    // IS_DEV && console.log('命中cache')
    let index = keys.indexOf(item)
    let seed = seeds[index]
    return seed
  } else {
    let seed = _seed++
    // IS_DEV && console.log('cache seed', seed)
    cache(item, seed)
    return seed
  }
}

function install (Vue) {
  Vue.mixin({
    beforeCreate () {
      this.__GEN_KEYS__ = []
    },
    methods: {
      gen_key (item) {
        this.__GEN_KEYS__.push(item)
        gen_key(item)
      }
    },
    beforeDestroy () {
      let inner_keys = this.__GEN_KEYS__
      inner_keys.forEach(item => {
        let i = keys.indexOf(item)
        if (i !== -1) {
          IS_DEV && console.log('删除item', item)
          keys.splice(i, 1)
          seeds.splice(i, 1)
        }
      })
    }
  })
}

export {
  install
}
