<template>
  <div class="news">
    <div class="news-banner"></div>
    <div class="flex">
      <div class="container">
        <el-tabs v-model="news_type" type="btn" @tab-click="tabClickHandler">
          <el-tab-pane label="全部" name="all">
            <common-list
              :current-group="news_type"
              group="all"
              :total="1000"
            >
              <news-list-cell v-for="item in 15" :key="item"></news-list-cell>
            </common-list>
          </el-tab-pane>
          <el-tab-pane label="公益会议" name="meetting">
            <common-list
              :current-group="news_type"
              group="meetting"
              :total="1000"
            >
              <news-list-cell v-for="item in 15" :key="item"></news-list-cell>
            </common-list>
          </el-tab-pane>
          <el-tab-pane label="公益培训" name="training">
            <common-list
              :current-group="news_type"
              group="training"
              :total="1000"
            >
              <news-list-cell v-for="item in 15" :key="item"></news-list-cell>
            </common-list>
          </el-tab-pane>
          <el-tab-pane label="公益招聘" name="recruitment">
            <common-list
              :current-group="news_type"
              group="recruitment"
              :total="1000"
            >
              <news-list-cell v-for="item in 15" :key="item"></news-list-cell>
            </common-list>
          </el-tab-pane>
          <el-tab-pane label="公益新闻" name="news">
            <common-list
              :current-group="news_type"
              group="news"
              :total="1000"
            >
              <news-list-cell v-for="item in 15" :key="item"></news-list-cell>
            </common-list>
          </el-tab-pane>
        </el-tabs>
      </div>
      <div class="aside pt20">
        <div class="none">{{page}}</div>
        <q-aside-column name="热门资讯"></q-aside-column>
      </div>
    </div>
  </div>
</template>
<script>
import {
  // cloneDeep,
  result,
  merge,
  debounce,
  isEqual
} from 'lodash'
let replace_params = []
export default {
  data () {
    return {
      news_type: 'all'
    }
  },
  computed: {
    page: vm => {
      let page = result(vm.$store.state.common_list, vm.news_type + '.page') || 1
      console.log('page click handler')
      vm.replace({ page })
      return page
    }
  },
  created () {
    this.initTab()
  },
  methods: {
    initTab () {
      let query = this.$route.query
      let page = query.page ? Number(query.page) : 1
      this.news_type = query.news_type ? query.news_type : 'all'
      this.replace({ news_type: this.news_type })
      this.$store.dispatch('setPage', { name: this.news_type, page })
    },
    tabClickHandler () {
      let query = this.news_type === 'all' ? {} : { news_type: this.news_type }
      this.replace(query)
      console.log('tab click handler')
    },
    // 避免重复的replace操作
    replace (params) {
      replace_params.push(params)
      // debounce
      this._replace()
    },
    _replace: debounce(function () {
      let query = merge.apply(this, replace_params)
      console.log('replace_params', replace_params)
      console.log('query', query)
      replace_params = []
      let oldQuery = this.$route.query
      let newQuery = query
      if (oldQuery.page) {
        oldQuery.page = Number(oldQuery.page)
      }
      // console.log('old', oldQuery)
      // console.log('new', newQuery)
      if (!isEqual(oldQuery, newQuery)) {
        this.$router.replace({ query })
      }
    }, 30)
    // todo: 切换tab的时候希望不要请求接口 / 除非列表为空
  }
}
</script>
<style lang="scss">
.news {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  .news-banner {
    background-color: lightblue;
    width: 1200px;
    height: 300px;
    img { width: 100%; height: 300px; }
  }
  .container {
    flex: 1;
    margin-right: 20px;
    background-color: #fff;
    padding-top: 50px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .aside {
    width: 350px;
  }
  // 自定义 tabs 样式
  .el-tabs__nav {
    display: flex;
    float: none;
  }
  .el-tabs__item {
    flex: 1;
    float: none;
    text-align: center;
    padding: 0;
    &.is-active {
      background-color: $--color-primary;
      color: #fff;
    }
  }
}
</style>
