import qs from 'qs'
import axios from 'axios'
import { Message } from 'element-ui'
import Vue from 'vue'
const API_ROOT = process.env.VUE_APP_API_ROOT
let api = axios.create({
  baseURL: API_ROOT,
  headers: {},
  // timeout: 1000,
  paramsSerializer: function (params) {
    return qs.stringify(params, { arrayFormat: 'repeat' })
  }
})
api.interceptors.request.use(request => {
  let state = require('bizStore').default.state
  let usercode = state.storage.usercode || ''
  let token = state.storage.token || ''
  let suffix = request.url.indexOf('?') > -1
    ? `&usercode=${usercode}&token=${token}`
    : `?usercode=${usercode}&token=${token}`
  if (Vue.prototype.IS_IE) {
    // 避免接口请求使用缓存 仅ie
    suffix += '_=' + Date.now()
  }
  request.url = request.url + suffix
  return request
})
api.interceptors.response.use(response => {
  return response.data.data
},
error => {
  let error_response = error.response
  let data = error.response && error_response.data
  if ((data || (data = {})).status >= 500) {
    Message.error({ message: '服务器错误, 请联系管理员' })
  }
  return Promise.reject(data)
})
export default api
