<template>
  <div class="common-list">
    <slot></slot>
    <!-- && data.length > 0 -->
    <div class="q-pagination">
      <el-pagination
        v-if="pagination"
        hide-on-single-page
        background
        layout="prev, pager, next, jumper"
        :current-page.sync="currentPage"
        :total="real_total"
        :page-size="page_size"
        @current-change="currentChange"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  name: 'CommonList',
  props: {
    data: {
      type: Array,
      default () {
        return []
      }
    },
    // 是否展示分页器
    pagination: {
      type: Boolean,
      default: true
    },
    total: {
      type: [Number, String],
      default () {
        return 0
      }
    },
    // 列表接口初始化完成
    init: {
      type: Boolean,
      default () {
        return false
      }
    },
    group: {
      type: String,
      default () {
        return 'default'
      }
    },
    currentGroup: {
      type: String,
      default () {
        return 'default'
      }
    }
  },
  data () {
    return {
      // currentPage: 1,
      page_size: 15
    }
  },
  computed: {
    real_total (vm) {
      console.log('real_total', vm.total > vm.max_total ? vm.max_total : vm.total)
      return vm.total > vm.max_total ? vm.max_total : vm.total
    },
    max_total (vm) {
      return vm.page_size * 200
    },
    currentPage: {
      get () {
        let state = this.$store.state.common_list
        if (state[this.group]) {
          let item = state[this.group]
          return item.page
        } else {
          return 1
        }
      },
      set (value) {
        this.$store.dispatch('setPage', { name: this.group, page: value })
      }
    }
  },
  methods: {
    getData (data) {
      if (data.length > this.page_size) {
        let start = (this.currentPage - 1) * this.page_size
        let end = this.currentPage * this.page_size
        return data.slice(start, end)
      }
      return data
    },
    currentChange (value) {
      this.$store.dispatch('setPage', { name: this.group, page: value })
      this.$emit('page-changed', value)
    },
    delaySetCurrentPage (pageNumber) {
      // console.log('delay set page')
      if (!this.init) {
        setTimeout(() => {
          this.delaySetCurrentPage(pageNumber)
        }, 250)
      } else {
        this.currentPage = pageNumber
      }
    },
    initPageNumber () {
      this.$store.dispatch('initPage', { name: this.group })
      if (this.group !== this.currentGroup) {
        return
      }
      let query = this.$route.query
      if (query.page) {
        let value = query.page
        if (Number(this.total) > 0) {
          this.currentPage = Number(value)
          this.currentChange(Number(value))
        } else {
          this.currentChange(Number(value))
          // 请求未完成total为0, 此时设置pageNumber将会永远是1
          // 于是我们延迟设置currentPage, 等待页面初始化完成
          this.delaySetCurrentPage(Number(value))
        }
      } else if (query.page_size) {
        let value = query.page_size
        this.page_size = Number(value)
      }
    }
  },
  created () {
    this.initPageNumber()
  }
}
</script>
<style lang="scss">
.common-list {
  position: relative;
  .q-pagination {
    margin-top: 15px;
    display: flex;
    justify-content: flex-end;
    width: 100%;
    padding-bottom: 20px;
  }
}
</style>
