import _ from 'lodash'
// 被认定为空的情况
// ''               String
// NaN              Number
// []               Array
// [{}]             Array
// {}               Object
// null             Object
// undefined
export function isEmptyObjectArray (value) {
  // [{}, '', null, NaN, undefined]
  for (var i = value.length - 1; i >= 0; i--) {
    let item = value[i]
    // 非空数据
    if (
      // 是数字类型却不是NaN
      (_.isNumber(item) && !isNaN(item)) ||
      // 有值, 却不是空对象空数组
      (item && !_.isEmpty(item))
    ) {
      return false
    }
  }
  return true
}
export function isEmpty (value) {
  if (
    // '', null, undefined
    (!_.isNumber(value) && !value) ||
    // NaN
    (_.isNumber(value) && isNaN(value)) ||
    // {}
    (_.isPlainObject(value) && _.keys(value).length === 0) ||
    // []
    (_.isArray(value) && value.length === 0) ||
    // [{}, '', null, NaN, undefined]
    (_.isArray(value) && value.length !== 0 && isEmptyObjectArray(value))
  ) {
    return true
  } else {
    return false
  }
}
