import apis from 'api/index'
import _ from 'lodash'
import ZRequest from 'util/ZRequest'
let state = {}
_.each(apis, api => {
  state[api.name] = {}
  state[api.name].status = ZRequest.INIT
  // 仅存储本次请求的数据
  if (api.type === ZRequest.LIST) {
    state[api.name].data = []
    state[api.name].count = 0
  } else if (api.type === ZRequest.DETAIL) {
    state[api.name].data = {}
  }
})
export default state
