/**
 * keep-alive · 改
 * 将缓存策略和路由的key状态(history.state.key)进行绑定
 */
export default {
  name: 'zbKeepAlive',
  abstract: true,
  created () {
    this.cache = Object.create({})
  },
  destroyed () {
    for (const key in this.cache) {
      const vnode = this.cache[key]
      vnode && vnode.componentInstance.$destroy()
    }
  },
  render () {
    const vnode = this.$slots.default ? this.$slots.default[0] : null
    if (vnode) {
      vnode.key = vnode.key || (vnode.isComment
        ? 'comment'
        : vnode.tag)
      // prevent vue-router reuse component
      const key = window.history.state && window.history.state.key
      if (this.cache[key]) {
        // console.log('一级命中, 命中 history.state.key')
        if (vnode.key === this.cache[key].key) {
          // console.log('二级命中, 命中 vnode.key')
          // restore vnode from cache
          vnode.componentInstance = this.cache[key].componentInstance
        } else {
          // replace vnode to cache
          this.cache[key].componentInstance.$destroy()
          this.cache[key] = vnode
        }
      } else {
        // cache new vnode
        // console.log('未命中缓存, 自行渲染, 并进行缓存')
        this.cache[key] = vnode
      }
      vnode.data.keepAlive = true
    }
    return vnode
  }
}
