          <img v-show="index < 3" :src="icons[index]" style="width: 28px; height: 28px;">
          <span v-show="index >= 3" style="padding-left: 10px;"> {{ index + 1}}</span>
        </el-col>
        <el-col :span="col_2" class="title">资讯标题{{index}}</el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  name: 'QAsideColumn',
  props: {
    name: String
  },
