import apis from 'api/index'
import _ from 'lodash'
import ZRequest from 'util/ZRequest'
import {
  REQUEST_STATE_CHANGE,
  SAVE_TO_DATA_POOL,
  REQUEST_SUCCESS,
  REQUEST_FAILURE
} from 'store/mutations'
let actions = {}
_.each(apis, api => {
  actions[ api.name ] = ({ commit }, params) => {
    // 此处要记录请求的状态
    commit(REQUEST_STATE_CHANGE, { name: api.name, status: ZRequest.LOADING })
    return api.send(params).then(response => {
      commit(REQUEST_STATE_CHANGE, { name: api.name, status: ZRequest.SUCCESS })
      commit(REQUEST_SUCCESS, { name: api.name, type: api.type, response })
      // ////////////////
      // 存储到数据池中 //
      // ////////////////
      // 如果该接口存在数据类型配置
      // 则需要将请求结果存储到数据池中
      if (api.data_type) {
        // 存储到数据池中
        commit(SAVE_TO_DATA_POOL, {
          // list or detail or other
          type: api.type,
          // 数据类型
          data_type: api.data_type,
          // 请求结果
          response
          // ,
          // 用于进行唯一标识的字段
          // 现在不需要了, zrequest将uuid统一代理到_uuid字段下
          // uuid: api.uuid
        })
      }
      return response
    }).catch(error => {
      commit(REQUEST_FAILURE, { name: api.name, type: api.type })
      commit(REQUEST_STATE_CHANGE, { name: api.name, status: ZRequest.FAILURE })
      return Promise.reject(error)
    })
  }
})
actions.set_apis_loading = ({ commit }, params) => {
  if (!Array.isArray(params)) {
    params = [params]
  }
  params.forEach(name => {
    commit(REQUEST_STATE_CHANGE, { name, status: ZRequest.LOADING })
  })
}

export default actions
