<template>
  <div class="command-line" v-show="visiable">
    <el-input
      ref="CommandInput"
      v-model="command"
      prefix-icon="el-icon-arrow-right"
      @blur="hideCommandLine"
      @keydown.native.enter="submitCommand"
    ></el-input>
  </div>
</template>
<script>
export default {
  name: 'CommandLine',
  data () {
    return {
      command: '',
      visiable: false
    }
  },
  created () {
    this.handlerCtrlP = this.showCommandLine.bind(this)
    this.handlerEsc = this.hideCommandLine.bind(this)
    this.addHotKey('ctrl + p', this.handlerCtrlP)
    this.addHotKey('esc', this.handlerEsc)
  },
  beforeDestroy () {
    this.removeHotKey('ctrl + p', this.handlerCtrlP)
    this.removeHotKey('esc', this.handlerEsc)
  },
  methods: {
    showCommandLine () {
      this.visiable = true
      let that = this
      setTimeout(() => {
        that.$refs.CommandInput.focus()
      })
    },
    hideCommandLine () {
      this.visiable = false
    },
    submitCommand () {
      this.hideCommandLine()
      console.log('run command', this.command)
      this.command = ''
    }
  }
}
</script>
<style lang="scss">
.command-line {
  position: fixed;
  top: 100px;
  left: 50%;
  margin-left: -400px;
  width: 800px;
  height: 80px;
  // background: #fff;
  // border: 2px solid #ccc;
  z-index: 99999999;
}
</style>
