// 将组件, 过滤器, 指令进行打包

import ZRequestMixin from 'mixin/ZRequest'
import Comps from 'comp'
import BizComps from 'bizComp'
import Filters from 'filters'
import BizFilters from 'bizFilters'
import Directive from 'directive'
import ForwardBackward from 'plugin/ForwardBackward'
import * as GenKey from 'plugin/GenKey'
import * as Emitter from 'plugin/Emitter'
import * as Modal from 'plugin/Modal'
import * as HotKey from 'plugin/HotKey'

function install (Vue) {
  Vue.mixin(ZRequestMixin)
  Vue.use(ForwardBackward)
  Vue.use(GenKey)
  Vue.use(HotKey)
  Vue.use(Emitter)
  Vue.use(Modal)
  Vue.use(Comps)
  Vue.use(Filters)
  Vue.use(Directive)
  Vue.use(BizComps)
  Vue.use(BizFilters)
}

export {
  install
}
